<template>
  <div class="max-w-md mx-auto my-3 overflow-hidden rounded shadow-lg">
    <div class="px-6 py-4">
      <div class="mb-2 text-sm text-center">
        You are invited for the wedding of
      </div>
      <div class="mb-2 text-5xl font-bold text-center text-gold font-heading">
        Deborah & Morgan
      </div>
      <div class="mb-2 text-sm leading-8 text-center">
        Please enter the RSVP Code
      </div>
      <div v-if="error" class="text-error">
        <p class="text-xs text-center">Invalid code</p>
      </div>

      <div class="text-center">
        <input
          class="inline-block mt-1 text-sm text-center text-gray-700 border-2 border-gray-500 rounded"
          placeholder="CODE"
          v-model="rsvpId"
        />
      </div>

      <div class="px-6 py-4 text-center">
        <button
          type="button"
          class="px-4 py-2 mr-1 font-normal text-white transition duration-300 ease-in-out bg-green-200 rounded btn-primary focus:outline-none focus:shadow-outline hover:bg-green-500"
          v-on:click="submitCode()"
        >
          Submit
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      rsvpId: null,
      error: null,
    };
  },
  mounted() {
    this.error = this.$route.query.error ? true : false;
  },
  methods: {
    submitCode() {
      this.$router.push({ path: `/rsvp/${this.rsvpId}` });
    },
  },
};
</script>