<template>
  <div
    v-if="guests"
    class="max-w-md mx-auto my-3 overflow-hidden rounded shadow-lg"
  >
    <div class="px-6 py-4">
      <div class="mb-2 text-sm text-center">
        You are invited for the wedding of
      </div>
      <div class="mb-2 text-5xl font-bold text-center text-gold font-heading">
        Deborah & Morgan
      </div>
      <div class="mb-2 text-sm leading-8 text-center">
        on Friday 16 April 2021 <br />
        10:30am ceremony at <br />
        Alexandra Headland Beach Zone <br />
        followed by 2.00pm celebration at <br />
        67 Okinja Road, Alexandra Headland <br />
        Queensland 4572 <br />
      </div>
      <p class="font-bold text-gray-700">Ceremony</p>
      <div v-for="(guest, i) in guests" v-bind:key="`Ceremony-${i}`">
        <p class="pt-3 text-sm text-gray-700">{{ guest.name }}</p>
        <div class="text-center">
          <label class="items-center mt-4">
            <input
              type="radio"
              class="mt-4"
              :name="`ceremonyattend-${i}`"
              v-model="guest.ceremony"
              :value="true"
            />
            <span class="pt-3 ml-2 text-sm text-gray-700">Attending</span>
          </label>
          <label class="items-center ml-5">
            <input
              type="radio"
              class="mt-4"
              :name="`ceremonyattend-${i}`"
              v-model="guest.ceremony"
              :value="false"
            />
            <span class="pt-3 ml-2 text-sm text-gray-700">Not Attending</span>
          </label>
        </div>
      </div>

      <p class="mt-5 font-bold text-gray-700">Celebration</p>
      <div v-for="(guest, i) in guests" v-bind:key="`Celebration-${i}`">
        <p class="pt-3 text-sm text-gray-700">{{ guest.name }}</p>
        <div class="text-center">
          <label class="items-center mt-4">
            <input
              type="radio"
              class="mt-4"
              :name="`celebrationattend-${i}`"
              v-model="guest.celebration"
              :value="true"
            />
            <span class="pt-3 ml-2 text-sm text-gray-700">Attending</span>
          </label>
          <label class="items-center ml-5">
            <input
              type="radio"
              class="mt-4"
              :name="`celebrationattend-${i}`"
              v-model="guest.celebration"
              :value="false"
            />
            <span class="pt-3 ml-2 text-sm text-gray-700">Not Attending</span>
          </label>
        </div>
        <div class="text-left">
          <label class="ml-5">
            <span class="pt-3 ml-2 text-xs text-gray-700"
              >Dietary Requirments</span
            >
            <input
              type="text"
              class="inline-block mt-1 ml-3 text-xs text-gray-700 border-2 border-gray-500 rounded"
              v-model="guest.dietary"
            />
          </label>
        </div>
      </div>
      <p class="mt-5 mb-5 text-xs">
        Due to fluid COVID-19 restrictions the event may get cancelled or moved
        within short notice, for us to contact you please leave your phone
        number with us
      </p>
      <label class="block">
        <span class="mt-8 font-bold text-gray-700">Mobile</span>
        <input
          type="tel"
          class="block mt-1 text-sm text-gray-700 border-2 border-gray-500 rounded"
          placeholder="0400000000"
          v-model="mobile"
        />
      </label>

      <div class="px-6 py-4 text-center">
        <button
          v-on:click="updateGuests()"
          type="button"
          class="px-4 py-2 mr-1 font-normal text-white transition duration-300 ease-in-out bg-green-200 rounded btn-primary focus:outline-none focus:shadow-outline hover:bg-green-500"
        >
          Submit
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      guests: null,
      mobile: "0400000000",
    };
  },
  async mounted() {
    this.getGuests(this.$route.params.rsvpId);
  },
  methods: {
    updateGuests() {
      let rsvpId = this.$route.params.rsvpId;
      var myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      var raw = JSON.stringify({
        mobile: this.mobile,
        guests: this.guests,
      });

      var requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw,
        redirect: "follow",
      };

      fetch(
        `https://lfwnxwpe20.execute-api.ap-southeast-2.amazonaws.com/rsvp/${rsvpId}`,
        requestOptions
      )
        .then((response) => response.text())
        .then((result) => {
          this.$router.push({ path: `/thanks` });
          console.log(result);
        })
        .catch((error) => console.log("error", error));
    },
    getGuests(rsvpId) {
      var myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      var requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };

      fetch(
        `https://lfwnxwpe20.execute-api.ap-southeast-2.amazonaws.com/rsvp/${rsvpId}`,
        requestOptions
      )
        .then((response) => response.json())
        .then((result) => {
          if (result.guests.length > 0) {
            this.guests = result.guests;
            this.mobile = result.mobile ? result.mobile : "0400000000";
          } else {
            this.$router.push({ path: `/`, query: { error: "true" } });
          }
        })
        .catch(() => {
          this.$router.push({ path: `/`, query: { error: "true" } });
        });
    },
  },
};
</script>